@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700);
.tooltip {
  box-sizing: border-box;
  position: absolute;
  padding: 5px 10px;
  color: var(--white);
  background: var(--black);
  z-index: 999;
  pointer-events: none;
}
.tooltip-enter {
  opacity: 0;
}
.tooltip-enter-active {
  opacity: 1;
}
.tooltip-exit {
  opacity: 1;
}
.tooltip-exit-active {
  opacity: 0;
}
.tooltip_table {
  color: var(--white);
}
.tooltip_table td:first-child {
  padding-right: 10px;
}

main {
  flex-grow: 1;
}
main > * {
  margin: 40px auto;
  max-width: 800px;
}

.home_header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-grow: 0;
  flex-shrink: 0;
  height: 250px;
  background: var(--black);
  color: var(--white);
}
.home_header > svg {
  margin-bottom: 5px;
}

.clickable {
  box-sizing: border-box;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 11pt;
  text-decoration: none;
}
.clickable {
  -webkit-transition: color var(--fast), background var(--fast), opacity var(--fast),
    border var(--fast), border-color var(--fast);
  transition: color var(--fast), background var(--fast), opacity var(--fast),
    border var(--fast), border-color var(--fast);
}

.clickable:hover,
.clickable:focus {
  color: var(--green) !important;
}
.clickable > svg:not(:first-child) {
  margin-left: 5px;
}
.clickable > svg:not(:last-child) {
  margin-right: 5px;
}
.clickable[data-button='true'] {
  min-width: 30px;
  min-height: 30px;
}
.clickable[data-button='true'][data-text='true'] {
  margin: 5px 10px;
  padding: 0 10px;
  border: solid 2px transparent;
  color: var(--green);
  font-weight: 500;
}
.clickable[data-button='true'][data-text='true']:hover,
.clickable[data-button='true'][data-text='true']:focus {
  border-color: var(--green);
  box-shadow: none;
}
.clickable[data-button='true'][data-text='false'] {
  color: var(--black);
}
.clickable[data-link='true'] {
  display: inline-block;
  color: var(--black);
  text-decoration: underline;
}

.banner {
  text-align: center;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}
.banner > a {
  margin-top: 20px !important;
}

.feature {
  display: flex;
  justify-content: center;
  align-items: center;
}
.pane {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50%;
  padding: 0 10px;
}
.video {
  width: 320px;
  height: 180px;
  max-width: 100%;
  max-height: 100%;
  background: var(--dark-gray);
}
.feature_header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.feature_header > svg {
  margin-right: 10px;
}

@media screen and (max-width: 650px) {
  .feature {
    flex-direction: column;
  }
  .pane {
    width: 100%;
    padding: 0;
  }
  .pane_first {
    order: -1;
    margin-bottom: 20px;
  }
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  box-shadow: var(--shadow);
}

.horizontal_line {
  content: '';
  width: 100%;
  border: none;
  height: 1px;
  background: var(--light-gray);
  z-index: -1;
}


.logo_small {
  color: var(--green);
  margin-right: 10px !important;
}
.title_text {
  color: var(--green);
}

.nav_button {
  position: relative;
  margin: 5px !important;
  padding: 3px 8px !important;
  text-decoration: none !important;
}
.nav_button > svg {
  margin-right: 8px !important;
}
.nav_button:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 2px;
  opacity: 0;
  background: var(--green);
  -webkit-transition: opacity var(--fast);
  transition: opacity var(--fast);
}
.nav_button[data-active='true']:after {
  opacity: 1;
}


.popup_overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #00000080;
  z-index: 99;
}
.popup_content {
  background: var(--white);
  position: absolute;
  box-shadow: var(--shadow);
  z-index: 100;
}


.model_item {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  width: 100%;
  height: 40px;
}
.model_summary {
  display: inline-flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  width: calc(100% - 30px - 30px);
}
.model_summary > * {
  line-height: 1em;
}


.model_select_button {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  background: var(--off-white);
  border-radius: 100%;
}
.model_select_button:hover,
.model_select_button:focus {
  box-shadow: none;
  background: var(--green);
  color: var(--white) !important;
}
.model_select_popup {
  width: 280px;
  min-height: calc((1 * 40px) + ((1 - 1) * 1px));
  max-height: calc((5 * 40px) + ((5 - 1) * 1px));
  overflow-x: hidden;
  overflow-y: auto;
}
.model_alert {
  height: 40px;
}


.page_header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  box-shadow: var(--shadow);
}
.page_header_column {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  height: 50px;
}
.page_header_column:nth-child(1) {
  justify-content: flex-start;
  padding: 0 10px;
}
.page_header_column:nth-child(2) {
  justify-content: center;
  flex-grow: 1;
}
.page_header_column:nth-child(3) {
  justify-content: flex-end;
  padding: 0 10px;
}

@media screen and (max-width: 650px) {
  .page_header_column:nth-child(1) {
    flex-grow: 1;
    order: 1;
  }
  .page_header_column:nth-child(2) {
    flex-direction: column;
    flex-basis: 100%;
    flex-grow: 0;
    height: unset;
    order: 3;
  }
  .page_header_column:nth-child(3) {
    flex-grow: 1;
    order: 2;
  }
  .page_header_column:first-child:last-child {
    justify-content: center;
  }
}

section {
  box-sizing: border-box;
  width: 100%;
  padding: 0 20px;
}
section[data-expanded='false'] {
  display: none;
}
.section_header {
  box-sizing: border-box;
  color: var(--gray);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: color var(--fast);
  transition: color var(--fast);
}
.section_header:hover {
  color: var(--green);
}
.section_header > svg {
  margin-right: 10px;
}
.section_header:before,
.section_header:after {
  content: '';
  flex-grow: 1;
  background: var(--light-gray);
  height: 1px;
  -webkit-transition: background var(--fast);
  transition: background var(--fast);
}
.section_header:hover:before,
.section_header:hover:after {
  background: var(--green);
}
.section_header:before {
  margin-right: 20px;
}
.section_header:after {
  margin-left: 20px;
}

.input {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  width: 100%;
  height: 30px;
  padding: 2px 0;
}
.input[data-expanded='true'] {
  height: calc(5 * 20px);
}
.input:before,
.input:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: -10;
  pointer-events: none;
  -webkit-transition: border-color var(--fast);
  transition: border-color var(--fast);
}
.input:before {
  border: solid 2px transparent;
}
.input[data-expanded='true']:before {
  border-color: var(--off-white);
}
.input:after {
  border-bottom: solid 2px var(--black);
}
.input[data-focused='true']:after {
  border-color: var(--green);
}
input,
textarea {
  box-sizing: border-box;
  flex-basis: 0;
  min-width: 0;
  flex-grow: 1;
  height: 100%;
  padding: 0 7.5px;
  background: none;
  border: none;
  outline: none;
  font-size: 11pt;
  resize: none;
  line-height: 20px;
}
input:focus,
textarea:focus {
  box-shadow: none;
}
.input[data-expanded='true'] > textarea {
  font-size: 10pt;
}
.input_button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
}

.search_bar {
  margin-bottom: 10px;
}

.table {
  width: 100%;
  overflow-x: auto;
  overflow-y: visible;
  max-height: calc((8 * 30px) + ((8 - 1) * 1px));
}
.thead,
.tbody {
  min-width: 600px;
}
.tr {
  display: flex;
  align-items: center;
  height: 30px;
}
.th {
  height: 28px;
  -webkit-transition: color var(--fast);
  transition: color var(--fast);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.table[data-sortable='true'] .th:hover {
  color: var(--green);
  cursor: pointer;
}
.th,
.td {
  box-sizing: border-box;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
}
.th[data-padded='true'],
.td[data-padded='true'] {
  padding: 0 5px;
}
.th > svg {
  flex-shrink: 0;
  margin-left: 3px;
}
.table[data-freeze='true'] .thead {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: var(--white);
  z-index: 2;
}
.table[data-freeze='true'] .th:first-child,
.table[data-freeze='true'] .td:first-child {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  background: var(--white);
  z-index: 1;
}


.gene_search_results_single_alert {
  height: calc(2 * 30px);
}

.vertical_line {
  content: '';
  width: 1px;
  height: calc(100% - 5px - 5px);
  margin: 5px 0;
  background: var(--light-gray);
  z-index: -1;
}

.gene_search_result_multi {
  display: flex;
  align-items: center;
  height: 30px;
}
.gene_search_result_multi_query {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-style: italic;
  flex: 0 0 60px;
  width: 60px;
}
.gene_search_result_multi_query[data-expanded='true'] {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(100% - 30px);
  width: calc(100% - 30px);
}
.gene_search_result_multi_results {
  display: inline-flex;
  align-items: center;
  width: calc(100% - 60px - 30px - 1px - 1px);
  height: 100%;
}
.gene_search_result_multi_results > .td {
  width: calc((100% - 30px - 30px - 30px - 1px - 1px) / 6);
}
.gene_search_result_multi_check {
  width: 30px;
}
.gene_search_result_multi_expand {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 30px;
}

@media screen and (max-width: 650px) {
  .gene_search_result_multi_results > .td {
    width: calc((100% - 30px - 30px - 30px - 1px - 1px) / 3);
  }
  .gene_search_result_multi_results > .td + .td {
    display: none;
  }
}


.gene_search_results_multi_alert {
  height: calc(1 * 30px);
  padding: 0 10px;
}


.alert {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 30px;
  color: var(--gray);
}
.alert > svg {
  margin-right: 10px;
}
.alert[data-error='true'] {
  color: var(--red);
}


.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 20px;
}




.rc-slider {
  box-sizing: border-box;
  position: relative;
  height: 35px;
  margin: 15px;
  touch-action: none;
  cursor: pointer;
}
.rc-slider * {
  box-sizing: border-box;
}
.rc-slider-rail {
  position: absolute;
  width: 100%;
  height: 5px;
  top: 5px;
  background: var(--light-gray);
}
.slider[data-reverse='true'] .rc-slider-rail {
  background: var(--green);
}
.rc-slider-track {
  position: absolute;
  height: 5px;
  left: 0;
  top: 5px;
  background: var(--green);
}
.slider[data-reverse='true'] .rc-slider-track {
  background: var(--light-gray);
}
.rc-slider-handle {
  position: absolute;
  width: 15px;
  height: 15px;
  top: 0;
  border-radius: 15px;
  background: var(--white);
  border: solid 2px var(--green);
}
.rc-slider-mark {
  position: absolute;
  top: 15px;
  left: 0;
  width: 100%;
}
.rc-slider-mark-text {
  position: absolute;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
}

.gene_network_info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 20px;
  margin-bottom: 10px;
}
.gene_network_info > span {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 20px;
}
.gene_network_info > span > svg {
  margin-right: 10px;
}

#graph {
  width: 100%;
  height: 500px;
  overflow: hidden;
  border: solid 1px var(--light-gray);
}
.graph_link_highlight {
  opacity: 0;
  -webkit-transition: opacity var(--very-fast);
  transition: opacity var(--very-fast);
}
.graph_link_highlight:hover {
  opacity: 0.5;
}
.graph_node_highlight {
  opacity: 0;
  -webkit-transition: opacity var(--very-fast);
  transition: opacity var(--very-fast);
}
.graph_node_highlight:hover {
  opacity: 0.5;
}





.experiment_search_results_single_alert {
  height: calc(2 * 30px);
}


#heatmap {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.heatmap_left_col {
  box-sizing: border-box;
  display: inline-block;
  width: 25%;
  padding-right: 10px;
  text-align: center;
}
.heatmap_right_col {
  box-sizing: border-box;
  display: inline-block;
  width: 75%;
  overflow-x: auto;
  overflow-y: auto;
  text-align: center;
}
.heatmap_row {
  display: flex;
  width: 100%;
  align-items: center;
}
.heatmap_cell {
  cursor: pointer;
}
#heatmap svg:hover .heatmap_cell {
  opacity: 0.35;
}
.heatmap_cell:hover {
  opacity: 1 !important;
}



.selected_experiment_info > * {
  margin-bottom: 20px;
}
.sample_table_info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 20px;
  margin-bottom: 10px;
}
.sample_table_info > span {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 20px;
}
.sample_table_info > span > svg {
  margin-right: 10px;
}





#volcano {
  width: 100%;
  height: 600px;
}
.volcano_dot {
  cursor: pointer;
}
#volcano_dots:hover .volcano_dot {
  opacity: 0.05;
}
.volcano_dot:hover {
  opacity: 1 !important;
  stroke: var(--black);
  stroke-width: 2;
}
.tick {
  font-size: 9pt;
}
#x_axis_label,
#y_axis_label {
  font-size: 12pt;
}





.detail_row {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 5px 0;
}
.detail_row > span {
  display: inline-block;
  word-break: break-word;
}
.detail_row > span:nth-child(1) {
  flex-basis: 20%;
  flex-grow: 0;
  flex-shrink: 0;
  margin-right: 10px;
}
.detail_row > span:nth-child(2) {
  flex-grow: 1;
}




* {
  --green: #26a36c;
  --blue: #2196f3;
  --red: #e91e63;
  --black: #000000;
  --dark-gray: #606060;
  --gray: #a0a0a0;
  --light-gray: #e0e0e0;
  --off-white: #f0f0f0;
  --white: #ffffff;

  --shadow: 0 0 4px 0 #00000040;

  --slow: 0.5s ease;
  --fast: 0.25s ease;
  --very-fast: 0.1s ease;

  font-family: 'Poppins';
}
html {
  height: 100%;
}
body {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 11pt;
  overflow-x: hidden;
  overflow-y: auto;
}
#root {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
}
a {
  color: unset;
  text-decoration: none;
}
*:focus {
  outline: none;
  box-shadow: inset 0 0 0 1px var(--green);
}
.medium {
  font-weight: 500;
}
.semibold {
  font-weight: 600;
}
.text_small {
  font-size: 9pt;
}
.text_medium {
  font-size: 13pt;
}
.text_large {
  font-size: 18pt;
}
.text_huge {
  font-size: 24pt;
}
.flip_horizontal {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}
.flip_vertical {
  -webkit-transform: scaleY(-1);
          transform: scaleY(-1);
}
.rotate_cw {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.rotate_ccw {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.nowrap {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
*[data-shade='true'] {
  background: var(--off-white);
}
*[data-highlight='true'] {
  font-weight: 600;
}
.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 20px;
}
table {
  border-collapse: collapse;
}
td {
  padding: 0;
}

